<template>
  <section class="modlude">
    <el-form :inline="true" :model="formSearch" class="form_inline" label-width="100px">
      <!-- <el-form-item label="" size="small" prop="xqId">
        <el-select v-model="formSearch.xqId" placeholder="">
          <el-option v-for="item in xqTree" :label="item.name" :value="item.id" :key="item.id"></el-option>
        </el-select>
      </el-form-item> -->

      <el-form-item>
        <el-button size="small" @click="addDialog=true"></el-button>
      </el-form-item>
      <el-form-item>
        <!-- <el-button size="small" type="danger" @click=""></el-button> -->
        <!-- <el-button size="small" type="danger" @click="deleInfor(false)"></el-button> -->
      </el-form-item>
    </el-form>
    <el-table :data="formData" style="width: 100%" stripe @selection-change="handleSelectionChange">
      <el-table-column type="selection" width="55"></el-table-column>
      <el-table-column prop="ip" label="IP"></el-table-column>
      <el-table-column prop="loginName" label=""></el-table-column>
      <el-table-column prop="loginPwd" label=""></el-table-column>
      <el-table-column prop="port" label=""></el-table-column>
      <el-table-column prop="" label="">
				<template slot-scope="scope">
          {{$root.getDateArray(scope.row.creator)[9]}}
				</template>
      </el-table-column>
      <el-table-column label="" fixed="right" width=150>
				<template slot-scope="scope">
					<el-button type="warning" size="small" @click="updateShowBox(scope.row)"></el-button>
					<el-button type="danger" size="small" @click="deleInfor(scope.row.id)"></el-button>
				</template>
			</el-table-column>
    </el-table>
    <!-- <paging @changePage = handleCurrentPage :get-total='total'></paging> -->
    <!--  -->
    <el-dialog title="" :visible.sync="addDialog" :close-on-click-modal="false">
      <div class="cont_box_right">
        <el-form label-position="right" :rules="rules" label-width="85px" :model="formPush" ref="addList"> 
          <el-form-item label="" size="small" prop="xqId">
            <el-select v-model="formPush.xqId" placeholder="">
              <el-option v-for="item in xqTree" :label="item.name" :value="item.id" :key="item.id"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="IP" size="small" prop="ip">
            <el-input v-model="formPush.ip" placeholder="IP"></el-input>
          </el-form-item>
          <el-form-item label="" size="small" prop="loginName">
            <el-input v-model="formPush.loginName" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" size="small" prop="loginPwd">
            <el-input v-model="formPush.loginPwd" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" size="small" prop="port">
            <el-input v-model="formPush.port" placeholder=""></el-input>
          </el-form-item>
        </el-form>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button size="medium " @click="addDialog = false"> </el-button>
        <el-button size="medium " @click="addList('addList')"> </el-button>
      </div>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="updateDialog" :close-on-click-modal="false">
      <div class="cont_box_right">
        <el-form label-position="right" :rules="rules" label-width="85px" :model="formUpdate" ref="update"> 
          <el-form-item label="" size="small" prop="xqId">
            <el-select v-model="formUpdate.xqId" placeholder="">
              <el-option v-for="item in xqTree" :label="item.name" :value="item.id" :key="item.id"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="IP" size="small" prop="ip">
            <el-input v-model="formUpdate.ip" placeholder="IP"></el-input>
          </el-form-item>
          <el-form-item label="" size="small" prop="loginName">
            <el-input v-model="formUpdate.loginName" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" size="small" prop="loginPwd">
            <el-input v-model="formUpdate.loginPwd" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" size="small" prop="port">
            <el-input v-model="formUpdate.port" placeholder=""></el-input>
          </el-form-item>
        </el-form>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button size="medium " @click="updateDialog = false"> </el-button>
        <el-button size="medium " @click="updateList('update')"> </el-button>
      </div>
    </el-dialog>
  </section>  
</template>

<script>
import paging from "../paging"
// import { getDevInfo,delDevInfo,addDevInfo,updateDevInfo,addReply,xqSelectList } from '../../url/api';
export default {
  data(){
    return{
      checkStrictly:false,
      bindRole:{},
      showmenuList:[],
      menuList:[],
      option1:[],
      xqTree:[],
      total:0,
      isAddorUpdate:1,//12
      formSearch:{
        xqId:'',
        current:1,
        size:10
      },
      formData: [],//
      deleBatch: [],//
      formUpdate:{},//
      total: 0,//
      pages:0,//
      addDialog:false,
      updateDialog:false,
      dialog:false,
      replyType:1,//1 2.
      formPush:{
      },
      dataTree:[],
      defaultProps: {//key
        children: 'data',
        label: 'name'
      },
      defaultProps2: {
        children: 'children',
        label: 'name',
        value: 'id',
        expandTrigger: 'hover',checkStrictly: true
      },
      reply:'',
      rules: {
        port:[{ required: true, message: ''}],
        loginName:[{ required: true, message: ''}],
        loginPwd:[{ required: true, message: ''}],
        ip:[{ required: true, message: ''}],
        type:[{ required: true, message: '',trigger: 'blur'}],
        xqId:[{ required: true, message: '',trigger: 'blur'}],
      },
      imageUrl:''
    }
  },
  methods:{
    getlist(){
      getDevInfo().then((res)=>{//
        console.log(res)
        if(res.data.code == 200){
          this.formData = res.data.data.filter((item)=>{
            return item
          })
          this.total = res.data.data.total
        }else{
          this.$message(res.data.msg);
        }
      })
    },
    getInit(){
      xqSelectList({}).then((res)=>{//
        console.log(res)
        if(res.data.code == 200){
          this.xqTree = res.data.data
          this.formSearch.xqId =Number(sessionStorage.getItem('defailXqid')||this.xqTree[0].id)
          this.getlist()
        }
      })
    },
    addList(addList){//node
      console.log(addList)
      this.$refs[addList].validate((valid) => {
        if (valid) {
          addDevInfo(this.formPush).then((res)=>{
            console.log(res)
            if(res.data.code == 200){
              this.getlist()
              this.$message({
                message: '',
                type: 'success'
              });
              
            }else{
              this.$message(':'+res.data.msg)
            }
          })
          this.addDialog = false
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },
    updateList(formName){//
      this.$refs[formName].validate((valid) => {
        if (valid) {
          let params = this.formUpdate
          updateDevInfo(params).then((res)=>{
            console.log(res)
            if(res.data.code == 200){
              this.$message({
                message: '',
                type: 'warning'
              });
              this.getlist()
            }
          })
        } else {
          console.log('error submit!!');
          return false;
        }
        this.updateDialog =false
      });
    },
    updateShowBox(item){//
      console.log(item)
      // this.formUpdate = JSON.parse(JSON.stringify(item));
      this.formUpdate = {
        id:item.id,
        ip:item.ip,
        loginName:item.loginName,
        loginPwd:item.loginPwd,
        port:item.port,
        xqId:item.xqId,
      }
      this.updateDialog = true
      
    },
   deleInfor(ids){//
      let arrId = []
      if(ids){
        arrId.push(ids)
      }else{
        if(this.deleBatch.length!=0){
          this.deleBatch.filter((item)=>{
            arrId.push(item.carId)
            return item
          })
        }
      }
      let params={
        infoIn:ids
      }
      this.$confirm('')
      .then(_ => {
        delDevInfo(params).then((res)=>{
          console.log(res)
          if(res.data.code == 200){
            this.$message('');
            this.getlist()
          }
        })
      })
      .catch(_ => {});
    },

    handleSelectionChange(val,self) {//
      this.deleBatch = val
      console.log(this.deleBatch)
    },
    handleCurrentPage(val){//
      this.formSearch.current=val
      this.getList()
    },
  },
  mounted(){
    this.getInit()
  },
  components:{
    paging
  }
}
</script>
<style scoped>
  .cont_box{
    width:100%;
    display: flex;
  }
  .cont_box_left,.cont_box_right{
    flex:1;
    background: transparent ;
    padding:10px;
  }
  .cont_box_left{
    margin-right:10px;
  }
  .margin10{
    margin-bottom:10px;
  }
  .marginRight{
    margin-bottom:10px;
    border-bottom: 1px solid #eee;
  }
  .cont_box_right .el-form-item{
    margin-bottom:20px;
  }

</style>


