<template>
  <section class="modlude">
    <el-form :inline="true" :model="formSearch" class="form_inline" label-width="100px">
      <el-form-item label="" size="small" prop="xqId">
        <el-select v-model="formSearch.xqId" placeholder="">
          <el-option v-for="item in xqTree" :label="item.name" :value="item.id" :key="item.id"></el-option>
        </el-select>
      </el-form-item>

      <el-form-item>
        <el-button size="small" @click="getlist"></el-button>
      </el-form-item>
      <el-form-item>
        <!-- <el-button size="small" type="danger" @click=""></el-button> -->
        <!-- <el-button size="small" type="danger" @click="deleInfor(false)"></el-button> -->
      </el-form-item>
    </el-form>
    <el-table :data="formData" style="width: 100%" stripe @selection-change="handleSelectionChange">
      <el-table-column type="selection" width="55"></el-table-column>
      <el-table-column prop="item2" label=""></el-table-column>
      <el-table-column prop="item1" label="IP"></el-table-column>
      <el-table-column prop="item3"label="">
      </el-table-column>
      <el-table-column label="" fixed="right" width=150>
				<template slot-scope="scope">
					<el-button type="primary" size="small" @click=""></el-button>
					<el-button type="warning" size="small" @click="deleInfor(scope.row.carId)"></el-button>
				</template>
			</el-table-column>
    </el-table>
    <paging @changePage = handleCurrentPage :get-total='total'></paging>
    <!--  -->
    <el-dialog title="" :visible.sync="updateDialog" :close-on-click-modal="false">
      <div class="cont_box_right">
        <el-form label-position="right" :rules="rules" label-width="85px" :model="formPush"> 
          <el-form-item label="" size="small" prop="adesc">
            <el-input type="textarea" v-model="reply" :rows="2" placeholder=""></el-input>
          </el-form-item>
        </el-form>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button size="medium " @click="updateDialog = false"> </el-button>
        <el-button size="medium " @click="deleInfor()"> </el-button>
      </div>
    </el-dialog>
  </section>  
</template>

<script>
import paging from "../paging"
// import { carParkSummaryData,delCar,addPropertyTypeList,updatePropertyTypeList,addReply,xqSelectList,orgTree } from '../../url/api';
export default {
  data(){
    return{
      checkStrictly:false,
      bindRole:{},
      showmenuList:[],
      menuList:[],
      option1:[],
      xqTree:[],
      total:0,
      isAddorUpdate:1,//12
      formSearch:{
        xqId:'',
        current:1,
        size:10
      },
      formData: [
        {
          item1:'192.168.33.121',
          item2:'IPC1',
          item3:''
        },
        {
          item1:'192.168.33.122',
          item2:'IPC2',
          item3:''
        },
        {
          item1:'192.168.33.123',
          item2:'IPC3',
          item3:''
        },
        {
          item1:'192.168.33.124',
          item2:'IPC4',
          item3:''
        },
        {
          item1:'192.168.33.125',
          item2:'IPC5',
          item3:''
        },
        {
          item1:'192.168.33.126',
          item2:'IPC6',
          item3:''
        },
        {
          item1:'192.168.33.127',
          item2:'IPC7',
          item3:''
        },
        {
          item1:'192.168.33.128',
          item2:'IPC8',
          item3:''
        },
        {
          item1:'192.168.33.129',
          item2:'IPC9',
          item3:''
        },
        {
          item1:'192.168.33.130',
          item2:'IPC10',
          item3:''
        },
      ],//
      deleBatch: [],//
      formUpdate:{},//
      total: 0,//
      pages:0,//
      addDialog:false,
      updateDialog:false,
      dialog:false,
      replyType:1,//1 2.
      formPush:{
      },
      dataTree:[],
      defaultProps: {//key
        children: 'data',
        label: 'name'
      },
      defaultProps2: {
        children: 'children',
        label: 'name',
        value: 'id',
        expandTrigger: 'hover',checkStrictly: true
      },
      reply:'',
      rules: {
        name:[{ required: true, message: ''}],
        type:[{ required: true, message: '',trigger: 'blur'}],
        xqId:[{ required: true, message: '',trigger: 'blur'}],
      },
      imageUrl:''
    }
  },
  methods:{
    getlist(){
      carParkSummaryData(this.formSearch).then((res)=>{
        console.log(res)
        if(res.data.code == 200){
          this.formData = res.data.data.records.filter((item)=>{
            return item
          })
          this.total = res.data.data.total
        }else{
          this.$message(res.data.msg);
        }
      })
    },
    getInit(){
      xqSelectList({}).then((res)=>{//
        console.log(res)
        if(res.data.code == 200){
          this.xqTree = res.data.data
          this.formSearch.xqId =Number(sessionStorage.getItem('defailXqid')||this.xqTree[0].id)
          // this.getlist()
        }
      })
      let org_tree={
        name:'',
        status:1
      }
      orgTree(org_tree).then((res)=>{//
        console.log(res)
        if(res.data.code == 200){
          this.dataTree = res.data.data
        }
      })
    },
    addList(addList){//node
      this.$refs[addList].validate((valid) => {
        if (valid) {
          addPropertyTypeList(this.formPush).then((res)=>{
            console.log(res)
            if(res.data.code == 200){
              this.getInit()
              this.$message({
                message: '',
                type: 'success'
              });
              
            }else{
              this.$message(':'+res.data.msg)
            }
          })
          this.addDialog = false
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },
    updateList(formName){//
      this.$refs[formName].validate((valid) => {
        if (valid) {
          if(this.formUpdate.gmtCreate){
            delete this.formUpdate.gmtCreate
          }
          let params = this.formUpdate
          updatePropertyTypeList(params).then((res)=>{
            console.log(res)
            if(res.data.code == 200){
              this.$message({
                message: '',
                type: 'warning'
              });
              this.getInit()
            }
          })
        } else {
          console.log('error submit!!');
          return false;
        }
        this.updateDialog =false
      });
    },
    updateShowBox(item){//
      // this.formUpdate = JSON.parse(JSON.stringify(item));
      this.formUpdate = {
        id:item.id,
      }
      this.updateDialog = true
      console.log(this.formUpdate)
    },
   deleInfor(ids){//
      let arrId = []
      if(ids){
        arrId.push(ids)
      }else{
        if(this.deleBatch.length!=0){
          this.deleBatch.filter((item)=>{
            arrId.push(item.carId)
            return item
          })
        }
      }
      this.$confirm('')
      .then(_ => {
        delCar(arrId).then((res)=>{
          console.log(res)
          if(res.data.code == 200){
            this.$message('');
            this.getInit()
          }
        })
      })
      .catch(_ => {});
    },

    handleSelectionChange(val,self) {//
      this.deleBatch = val
      console.log(this.deleBatch)
    },
    handleCurrentPage(val){//
      this.formSearch.current=val
      this.getList()
    },
  },
  mounted(){
    this.getInit()
    this.total=10
  },
  components:{
    paging
  }
}
</script>
<style scoped>
  .cont_box{
    width:100%;
    display: flex;
  }
  .cont_box_left,.cont_box_right{
    flex:1;
    background: transparent ;
    padding:10px;
  }
  .cont_box_left{
    margin-right:10px;
  }
  .margin10{
    margin-bottom:10px;
  }
  .marginRight{
    margin-bottom:10px;
    border-bottom: 1px solid #eee;
  }
  .cont_box_right .el-form-item{
    margin-bottom:20px;
  }

</style>


